<template>
  <div>
    <!---->
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>基础设置
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon> 机构设置
      </BreadcrumbItem>
      <span class="screenContent">
        <img
          style="width: 11px;margin-right: 8px;margin-left: 10px;"
          src="../assets/day.png"
        >
        {{ curTime }}</span>
      <span class="screenContent">
        <img
          style="width: 10.5px;margin-right: 8px"
          src="../assets/screen.png"
        >
        <a
          href="javascript:void(0)"
          @click="isScreen = !isScreen"
        >
          筛选
          <Icon type="ios-arrow-down"></Icon>
        </a>
      </span>
    </Breadcrumb>
    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="62"
      >
        <Row>
          <Col
            :xs="{ span: 5 }"
            :lg="{ span: 6}"
          >
          <FormItem label="机构名称">
            <Input
              v-model="searchData.orgName"
              clearable
              placeholder="请填写机构名称"
            ></Input>
          </FormItem>
          </Col>
          <Col
            :xs="{ span: 11, offset: 1 }"
            :lg="{ span: 6}"
          >
          <FormItem label="机构性质">
            <Select
              v-model="searchData.orgType"
              clearable
              filterable
              placeholder="请选择机构性质"
            ><Option
              v-for="item in characters"
              :key="item.value"
              :value="item.value"
            >{{ item.label }}</Option></Select>
          </FormItem>
          </Col>
          <Col
            style="text-align: right"
            :xs="{ span: 1}"
            :lg="{ span: 2, offset: 1 }"
          >
          <Button
            type="primary"
            @click="isShowScreen"
          >
            确认
          </Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="createData"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="modifyData"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
        @click="delData"
      >
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(1)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/organization/exportOrgListExcel')">
        导出
      </Button>
      <Table
        ref="checkTable"
        highlight-row
        class="table"
        :columns="columns"
        :data="data"
        @on-current-change="selectRow"
      ></Table>
      <!-- 分页 -->
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
      <!--详情弹出框-->
      <Modal
        v-model="selTypeMod"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span v-if="!isDisabled && isCreate">请设置机构信息</span>
          <span v-else>机构信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          :value="curTab"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >
            <Form
              ref="form"
              :model="form"
              :rules="ruleValidateType"
              :label-width="80"
            >
              <Row>
                <Col span="12">
                <FormItem
                  label="机构名称"
                  prop="orgName"
                >
                  <Input
                    v-model.trim="form.orgName"
                    :disabled="isDisabled"
                    placeholder="请填写机构名称"
                  ></Input>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem
                  label="机构性质"
                  prop="orgType"
                >
                  <Select
                    v-model="form.orgType"
                    clearable
                    :disabled="isDisabled"
                    :transfer="transferValue"
                    filterable
                  >
                    <Option
                      v-for="item in characters"
                      :key="item.value"
                      :value="item.value"
                    >{{ item.label }}</Option>
                  </Select>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="24">
                <FormItem label="机构地址">
                  <Input
                    v-model.trim="form.orgAddr"
                    :disabled="isDisabled"
                    type="textarea"
                    :autosize="{minRows: 1,maxRows: 5}"
                    placeholder="请填写机构地址"
                  ></Input>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="24">
                <FormItem label="备注">
                  <Input
                    v-model.trim="form.orgRemark"
                    :disabled="isDisabled"
                    type="textarea"
                    :autosize="{minRows: 1,maxRows: 5}"
                    placeholder="请填写备注"
                  ></Input>
                </FormItem>
                </Col>
              </Row>
            </Form>
          </TabPane>
          <TabPane
            class="uploadStyle"
            label="附件"
            name="name2"
          >
            <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="form" />
          </TabPane>
        </Tabs>
        <p
          slot="footer"
          style="text-align: left"
        >
          <Button icon="md-print">
            打印
          </Button>
          <Button icon="md-exit" @click="exportPdf">
            导出
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="selTypeMod = false"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="submitType('form')"
          >
            确认
          </Button>
        </p>
      </Modal>
      <!--/详情弹出框-->
    </Content>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
import excelExcel from '@/mixins/exportExcel.js'
export default {
  name: 'OrganizeManage',
  components: {
    uploadFile
  },
  mixins: [excelExcel],
  data() {
    return {
      // 分页的总数
      total: 0,
      // 当前tab
      curTab: 'name1',
      isDisabled: false,
      // 是否选择行
      isSelected: false,
      transferValue: true,
      characters: [
        {
          value: 1,
          label: '政府机关'
        },
        {
          value: 3,
          label: '合作组织'
        },
        {
          value: 4,
          label: '企业法人'
        },
        {
          value: 2,
          label: '事业法人'
        }
      ],
      // 当前对象
      curDate: {},
      form: {
        orgName: '',
        orgType: '',
        orgAddr: '',
        orgRemark: ''
      },
      ruleValidateType: {
        orgName: [
          { required: true, message: '请填写机构名称', trigger: 'change' }
        ],
        orgType: [
          { type: 'number', required: true, message: '请选择机构性质', trigger: 'change' }
        ]
      },
      // 筛选内容
      searchData: {
        orgType: '',
        orgName: '',
        ascending: false,
        orderBy: 'orgId',
        pageNo: 1,
        pageSize: 10
      },
      isScreen: false,
      curTime: '',
      showContact: false,
      selTypeMod: false,
      checkText: '提交审核',
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          indexMethod: this.indexMethod
        },
        {
          title: '机构名称',
          key: 'orgId',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo(params.row)
                }
              }
            }, params.row.orgName);
          }
        },
        {
          width: 160,
          title: '机构性质',
          key: 'orgTypeName'
        },
        {
          title: '机构地址',
          key: 'orgAddr'
        }
      ],
      data: [
      ],
      // 当前选择的行
      currentRow: {},
      //  新增|编辑
      isCreate: false
    }
  },
  mounted() {
    // 获取列表
    this.searchList()
    var myDate = new Date();
    const time = myDate.toLocaleString('chinese', { hour12: false });
    this.curTime = time.substring(0, 15)
  },
  methods: {
    pageNoChange(pageNo) {
      if (pageNo) {
        this.searchData.pageNo = pageNo
        this.searchList()
      }
    },
    pageSizeChange(pageSize) {
      if (pageSize) {
        this.searchData.pageSize = pageSize
        this.searchList()
      }
    },
    // 删除数据
    delData() {
      const that = this
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要删除的内容');
        return
      }
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/organization/deleteOrganization/' + that.currentRow.orgId, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.isSelected = false;
              that.$Message.success(res.msg);
              that.searchList()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            that.$Spin.hide();
          }, that)
        }
      });
    },
    searchList(callback) {
      const that = this;
      that.$Spin.show();
      that.commonUtil.ajaxPostUtil('/organization/searchOrganization/list', that.searchData, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          for (const item of res.data.orgList) {
            const orgType = item.orgType
            if (orgType === 1) {
              item.orgTypeName = '政府机关'
            } else if (orgType === 2) {
              item.orgTypeName = '事业法人'
            } else if (orgType === 3) {
              item.orgTypeName = '合作组织'
            } else if (orgType === 4) {
              item.orgTypeName = '企业法人'
            }
          }
          const listData = res.data
          that.total = listData.totalCount
          that.data = listData.orgList
          if (callback) {
            callback(listData.orgList)
          }
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
      that.commonUtil.getAuditCountApi(that)
    },
    // 选中一行进行修改数据
    selectRow(currentRow) {
      this.currentRow = currentRow;
      this.isSelected = true;
    },
    // 修改数据
    modifyData() {
      const row = Object.assign({}, this.currentRow);
      // 显示弹出框名称
      this.isCreate = false;
      this.isDisabled = false;
      this.clearFormData();
      if (!this.isSelected) {
        this.$Message.warning('您未选择需要修改的内容');
        return
      }
      this.showData(row)
    },
    // 导出
    exportPdf() {
    },
    clearFormData() {
      this.form = {
        orgName: '',
        orgType: '',
        orgAddr: '',
        orgRemark: ''
      }
      this.$refs['form'].resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    },
    createData() {
      this.clearFormData();
      this.curTab = 'name1'
      // 是否可编辑
      this.isDisabled = false;
      // 是否显示 修改 弹出框名称
      this.isCreate = true;
      this.selTypeMod = true;
    },
    submitType(name) {
      const that = this;
      const form = that.form
      form.fileIds = that.$refs.uploadFile.getFileIds()
      that.$refs[name].validate((valid) => {
        if (valid) {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          // 新增
          if (that.isCreate) {
            that.commonUtil.ajaxPostUtil('/organization/addOrganization', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.selTypeMod = false;
                that.$Message.success(res.msg);
                that.searchList()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          } else {
            const copyForm = Object.assign({}, form);
            copyForm.orgId = that.currentRow.orgId
            that.commonUtil.ajaxPutUtil('/organization/updateOrganization', copyForm, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.selTypeMod = false;
                that.$Message.success(res.msg);
                that.searchList()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
          that.currentRow = {}
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    // 筛选按钮
    isShowScreen() {
      this.searchList()
    },
    showInfo(row) {
      const data = Object.assign({}, row)
      this.clearFormData()
      this.isDisabled = true;
      this.showData(data)
    },
    showData(data) {
      this.form = data
      this.$nextTick(() => {
        this.$refs.uploadFile.getFileItems()
      })
      this.selTypeMod = true
    }
  }
}
</script>



